<template>
  <div id="home">
    <div class="home_info">
      <p>等级：{{userData.level}}</p>
      <p>姓名：{{userData.username}}</p>
      <p>推荐码： {{userData.referral_code}}</p>
      <i class="el-icon-setting home_info_icon" @click="jumpLink('settings')"></i>
    </div>
    <el-carousel arrow="never" trigger="click" height="150px" indicator-position="none">
      <el-carousel-item>
        <img src="../assets/bg1.jpg" alt="">
      </el-carousel-item>
      <el-carousel-item>
        <img src="../assets/bg1.jpg" alt="">
      </el-carousel-item>
      <el-carousel-item>
        <img src="../assets/bg1.jpg" alt="">
      </el-carousel-item>
    </el-carousel>
    <div class="home_notice">
      <div class="home_notice_title">公告: </div>
      <marquee behavior="scroll" scrollamount="5" direction="left" class="home_notice_main">
        本系统只是客户商家信誉体系评定推送工具，全为自由自愿加入.所有商业环节与系统无关，系统不收费,不卖产品，无分销,无代理.无奖金.为社交私域流量新生态 打造精准粉丝！各自诚信为本!感恩遇见!只是提供互联网本质属性的工具。所有社交原则自律、一经投诉及查实出假，大，空问题，将冻结账号。
      </marquee>
    </div>
    <div class="home_content">
      <button class="home_content_btn home_content_btn-primary" @click="jumpLink('register')">
        <i class="home_content_btn_icon el-icon-edit"></i>
        帮助注册
      </button>
      <button class="home_content_btn home_content_btn-success" @click="jumpLink('applyupgrade')">
        <i class="home_content_btn_icon el-icon-success"></i>
        申请升级
      </button>
      <button class="home_content_btn home_content_btn-search" @click="jumpLink('reviewupgrade')">
        <i class="home_content_btn_icon el-icon-search"></i>
        审核升级
      </button>
      <button class="home_content_btn home_content_btn-share" @click="jumpLink('myteam')">
        <i class="home_content_btn_icon el-icon-share"></i>
        我的团队
      </button>
    </div>
  </div>
</template>

<script>
import { getInfo } from '../api/index.js'
export default {
  data () {
    return {
      userData: {}
    }
  },
  methods: {
    jumpLink: function(link) {
      this.$router.push({path: '/' + link})
    }
  },
  created() {
    getInfo({}).then(res => {
      if (res.is_login) {
        this.userData = res.data
        localStorage.setItem('userData', JSON.stringify(res.data))
      }
      localStorage.setItem('is_login', res.is_login)
    })
  }
}
</script>

<style scoped>
  .home_info {
    width: 100%;
    background-image: url('../assets/bg.gif');
    background-size: 100% auto;
    background-position: 100% 50%;
    background-repeat:no-repeat;
    background-color: #ebfbff;
    padding: .2rem;
    box-sizing: border-box;
    position: relative;
  }

  .home_info p {
    line-height: 1.5;
  }

  .home_notice {
    width: 100%;
    background-color: #fff;
    display: flex;
    line-height: 2.5;
    border-top: 1px solid #f4f4f4;
    border-bottom: 1px solid #f4f4f4;
  }

  .home_notice_title {
    width: 15%;
    text-align: right;
    padding-right: 5px;
  }

  .home_notice_main {
    width: 85%;
  }

  .home_content {
    width: 100%;
    margin: .5rem 0;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-around;
  }

  .home_content_btn {
    width: 35%;
    font-size: .28rem;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    background-color: #fff;
    border-radius: 10px;
    padding: .7rem 0;
    cursor: pointer;
  }

  .home_content_btn:nth-child(1), .home_content_btn:nth-child(2) {
    margin-bottom: .5rem;
  }

  .home_content_btn_icon {
    font-size: .5rem;
    margin-bottom: .2rem;
  }

  .home_content_btn-primary {
    color: #fff;
    background-color: #409eff;
    border-color: #409eff;
  }

  .home_content_btn-primary:hover {
    background: #66b1ff;
    border-color: #66b1ff;
    color: #fff;
  }

  .home_content_btn-success {
    color: #fff;
    background-color: #e6a23c;
    border-color: #e6a23c;
  }

  .home_content_btn-success:hover {
    background: #ebb563;
    border-color: #ebb563;
    color: #fff;
  }

  .home_content_btn-search {
    color: #fff;
    background-color: #67c23a;
    border-color: #67c23a;
  }

  .home_content_btn-search:hover {
    background: #85ce61;
    border-color: #85ce61;
    color: #fff;
  }
  
  .home_content_btn-share {
    color: #fff;
    background-color: #909399;
    border-color: #909399;
  }

  .home_content_btn-share:hover {
    background: #a6a9ad;
    border-color: #a6a9ad;
    color: #fff;
  }

  #home >>> .el-carousel__item {
    display: flex;
    justify-content: center;
    align-items: center;
  }

  .home_info_icon {
    position: absolute;
    right: .2rem;
    top: .2rem;
    font-size: .5rem;
    color: #fff;
    cursor: pointer;
  }
</style>



