<template>
  <div class="ExitData">
    <TitleBtn title="我的资料"></TitleBtn>
    <div class="ExitData_content">
      <div class="ExitData_content_title">账号设置</div>
      <el-form label-position="right" :rules="rules" ref="form" :model="form" label-width="100px">
        <el-form-item label="姓名:" prop="username">
          <el-input v-model="form.username"></el-input>
        </el-form-item>
        <el-form-item label="微信号:" prop="weixin">
          <el-input v-model="form.weixin"></el-input>
        </el-form-item>
        <div class="ExitData_content_submit">
          <el-button style="width: 100%;" type="danger" @click="submitForm('form')">确认修改</el-button>
        </div>
      </el-form>
    </div>
  </div>
</template>

<script>
import TitleBtn from '@/components/TitleBtn'
import { editInfo, getInfo } from '../api/index.js'
export default {
  components: {
    TitleBtn
  },
  data () {
    return {
      lastusername: '',
      lastweixin: '',
      form: {
        username: '',
        weixin: ''
      },
      rules: {
        username: [
          { required: true, message: '请输入姓名', trigger: 'blur' },
          { min: 2, max: 20, message: '请输入正确的姓名', trigger: 'blur' }
        ],
        weixin: [
          { required: true, message: '请输入微信号码', trigger: 'blur' },
          { min: 6, max: 20, message: '请输入正确的微信号码', trigger: 'blur' }
        ]
      }
    }
  },
  methods: {
    submitForm(formName) {
      this.$refs[formName].validate((valid) => {
        if (valid) {
          if (this.lastusername != this.form.username || this.lastweixin != this.form.weixin) {
            editInfo({
              username: this.form.username,
              weixin: this.form.weixin
            }).then(res => {
              if (res.code == 0) {
                this.$message({
                  message: res.msg,
                  type: 'success',
                  duration: 1500
                })
                getInfo({}).then(res => {
                  this.userData = res.data
                  localStorage.setItem('userData', JSON.stringify(res.data))
                })
                this.$refs[formName].resetFields()
              } else {
                this.$message.error({message: res.msg, duration: 1500})
              }
            })
          }
        }
      })
    }
  },
  created() {
    this.userData = JSON.parse(localStorage.getItem('userData'))
    this.lastusername = this.userData.username
    this.lastweixin = this.userData.weixin
    this.form.username = this.userData.username
    this.form.weixin = this.userData.weixin
  }
}
</script>

<style scoped>
  .ExitData_content_title {
    padding: .3rem .2rem;
    color: #999999;
  }

  .is-required {
    border-top: 1px solid #f4f4f4;
    border-bottom: 1px solid #f4f4f4;
  }

  .ExitData >>> .el-input__inner {
    border: none;
  }

  .ExitData >>> .el-form-item__label, .ExitData >>> .el-form-item__content {
    line-height: 50px;
    font-size: 16px;
  }

  .ExitData_content_submit {
    width: calc(100% - .2rem);
    margin: 0 auto;
  }
</style>



