<template>
  <div class="titleBtn">
    <i class="el-icon-arrow-left titleBtn_icon" @click="backPage"></i>
    {{title}}
  </div>
</template>

<script>
export default {
  props: ['title'],
  data () {
    return {
    }
  },
  methods: {
    backPage() {
      this.$router.go(-1)
    }
  }
}
</script>

<style scoped>
  .titleBtn {
    height: 40px;
    line-height: 40px;
    font-size: 14px;
    background-color: #ffffff;
    position:relative;
    text-align: center;
    border-bottom: 1px solid #efefef;
  }

  .titleBtn_icon {
    position:absolute;
    top: 50%;
    left: .15rem;
    transform: translateY(-50%);
    font-size: 20px;
    font-weight: 600;
    cursor: pointer;
  }
</style>



