<template>
  <div class="ReviewUpgrade">
    <TitleBtn title="审核升级"></TitleBtn>
    <div class="ReviewUpgrade_top">
      <el-button type="warning" @click="jumpLink('reviewhistory')">审核历史</el-button>
    </div>
    <div class="ReviewUpgrade_content" v-if="checkLevelList.length > 0">
      <div class="ReviewUpgrade_content_list" v-for="(item, index) in checkLevelList" :key="item.id">
        <p>对方ID: {{item.id}}</p>
        <p>姓名: {{item.username}}</p>
        <p>准等级: {{item.up_level}}</p>
        <p>手机号码: {{item.mobile}}</p>
        <p>微信号: {{item.weixin}}</p>
        <div class="ReviewUpgrade_content_list_btn">
          <el-button type="success" @click="checkLevel(index, 0)">同意</el-button>
          <el-button type="danger" @click="checkLevel(index, 1)">拒绝</el-button>
        </div>
      </div>
    </div>
    <NoData v-if="checkLevelList.length <= 0"></NoData>
  </div>
</template>

<script>
import TitleBtn from '@/components/TitleBtn'
import NoData from '@/components/NoData'
import { checkLevelList, checkLevel } from '../api/index.js'
export default {
  components: {
    TitleBtn,
    NoData
  },
  data () {
    return {
      checkLevelList: []
    }
  },
  methods: {
    jumpLink: function(link) {
      this.$router.push({path: '/' + link})
    },
    checkLevel(index, repulse) {
      let msg = repulse ? '是否拒绝此用户升级' : '是否同意此用户升级'
      this.$confirm(msg, '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      }).then(() => {
        checkLevel({
          id: this.checkLevelList[index].id,
          repulse: repulse
        }).then(res => {
          if (res.code == 0) {
            this.$message({
              message: res.msg,
              type: 'success'
            })
            checkLevelList({}).then(res => {
              if (res.code == 0) {
                this.checkLevelList = res.data
              }
            })
          } else {
            this.$message.error(res.msg)
          }
        })
      }).catch(() => {})
    }
  },
  created() {
    checkLevelList({}).then(res => {
      if (res.code == 0) {
        this.checkLevelList = res.data
      }
    })
  }
}
</script>

<style scoped>
  .ReviewUpgrade_top {
    width: 100%;
    display: flex;
    justify-content: flex-end;
    margin-top: .1rem;
  }

  .ReviewUpgrade_top >>> .el-button {
    padding: .15rem .5rem;
    margin-right: .1rem;
  }

  .ReviewUpgrade_content_list {
    width: 85%;
    background-color: #f2f2f2;
    margin: .4rem auto 0;
    padding: .2rem;
    line-height: 1.4;
    position: relative;
  }

  .ReviewUpgrade_content_list_btn {
    height: 100%;
    position: absolute;
    right: .2rem;
    top: 0;
    padding: .2rem 0;
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    box-sizing: border-box;
  }

  .ReviewUpgrade_content_list_btn >>> .el-button {
    padding: .15rem .25rem;
  }

  .el-button+.el-button {
    margin-left: 0;
  }
</style>



