import Vue from 'vue'
import Router from 'vue-router'
import Home from '@/components/Home'
import Register from '@/components/Register'
import Settings from '@/components/Settings'
import ExitData from '@/components/ExitData'
import ChangePW from '@/components/ChangePassword'
import ApplyUpgrade from '@/components/ApplyUpgrade'
import MerchantInfo from '@/components/MerchantInfo'
import MyTeam from '@/components/MyTeam'
import ReviewUpgrade from '@/components/ReviewUpgrade'
import ReviewHistory from '@/components/ReviewHistory'
import Login from '@/components/Login'

Vue.use(Router)

const router = new Router({
  routes: [
    {
      path: '/',
      name: '首页',
      component: Home,
      meta: {
        requireAuth: true
      },
    },
    {
      path: '/register',
      name: '帮助注册',
      component: Register,
      meta: {
        requireAuth: true
      },
    },
    {
      path: '/settings',
      name: '设置',
      component: Settings,
      meta: {
        requireAuth: true
      },
    },
    {
      path: '/exitdata',
      name: '修改信息',
      component: ExitData,
      meta: {
        requireAuth: true
      },
    },
    {
      path: '/changepw',
      name: '修改密码',
      component: ChangePW,
      meta: {
        requireAuth: true
      },
    },
    {
      path: '/applyupgrade',
      name: '申请升级',
      component: ApplyUpgrade,
      meta: {
        requireAuth: true
      },
    },
    {
      path: '/merchantinfo',
      name: '商家信息',
      component: MerchantInfo,
      meta: {
        requireAuth: true
      },
    },
    {
      path: '/myteam',
      name: '我的团队',
      component: MyTeam,
      meta: {
        requireAuth: true
      },
    },
    {
      path: '/reviewupgrade',
      name: '审核升级',
      component: ReviewUpgrade,
      meta: {
        requireAuth: true
      },
    },
    {
      path: '/reviewhistory',
      name: '审核历史',
      component: ReviewHistory,
      meta: {
        requireAuth: true
      },
    },
    {
      path: '/login',
      name: '登录',
      component: Login
    }
  ]
})

router.beforeEach((to, from, next) => {
  if (to.meta.requireAuth) {
    if (localStorage.getItem('is_login') === 'true') {
      next()
    } else {
      next({path: '/login'})
    }
  } else {
    next()
  }
})

export default router



