<template>
  <div class="ChangePassword">
    <TitleBtn title="修改密码"></TitleBtn>
    <div class="ChangePassword_content">
      <el-form label-position="top" :rules="rules" ref="form" :model="form" label-width="80px">
        <el-form-item label="您的旧密码:" prop="oldpassword">
          <el-input v-model="form.oldpassword"></el-input>
        </el-form-item>
        <el-form-item label="您的新密码:" prop="newpassword">
          <el-input type="password" v-model="form.newpassword"></el-input>
        </el-form-item>
        <el-form-item label="确认密码:" prop="checkPass">
          <el-input type="password" v-model="form.checkPass"></el-input>
        </el-form-item>
        <el-form-item class="">
          <el-button class="ChangePassword_content_submit" type="danger" @click="submitForm('form')">确认修改</el-button>
        </el-form-item>
      </el-form>
    </div>
  </div>
</template>

<script>
import TitleBtn from '@/components/TitleBtn'
import { editpwd } from '../api/index.js'
export default {
  components: {
    TitleBtn
  },
  data () {
    var validatePass2 = (rule, value, callback) => {
      if (value === '') {
        callback(new Error('请再次输入密码'))
      } else if (value !== this.form.newpassword) {
        callback(new Error('两次输入密码不一致!'))
      } else {
        callback()
      }
    }
    return {
      form: {
        oldpassword: '',
        newpassword: '',
        checkPass: ''
      },
      rules: {
        oldpassword: [
          { required: true, message: '请输入旧的密码', trigger: 'blur' },
          { min: 4, max: 20, message: '请输入正确的密码', trigger: 'blur' }
        ],
        newpassword: [
          { required: true, message: '请输入新的密码', trigger: 'blur' },
          { min: 4, max: 20, message: '请输入正确的密码', trigger: 'blur' }
        ],
        checkPass: [
          { required: true, validator: validatePass2, trigger: 'blur' }
        ]
      }
    }
  },
  methods: {
    submitForm(formName) {
      this.$refs[formName].validate((valid) => {
        if (valid) {
          editpwd({
            old_password: this.form.oldpassword,
            new_password: this.form.newpassword
          }).then(res => {
            if (res.code == 0) {
              this.$message({
                message: res.msg,
                type: 'success'
              })
              this.$refs[formName].resetFields()
            } else {
              this.$message.error({message: res.msg, duration: 1500})
            }
          })
        }
      })
    }
  }
}
</script>

<style scoped>
  .ChangePassword_content {
    width: 90%;
    margin: .2rem auto 0;
  }

  .ChangePassword >>> .el-form-item__error {
    top: -70%;
    right: 0;
    left: auto;
  }

  .ChangePassword >>> .el-form--label-top .el-form-item__label {
    padding: 0;
  }

  .el-form-item {
    margin: 0;
  }

  .ChangePassword_content_submit {
    margin-top: .4rem;
    width: 100%;
  }
</style>



