  </div>
</template>

<script>
export default {
  props: {
    'text': {
      default: '您还没有相关数据~'
    }
  },
  data () {
    return {
    }
  },
  methods: {
  }
}
</script>

<style scoped>
  .NoData {
    margin: 2.5rem 0 0;
    display: flex;
    flex-direction: column;
    align-items: center;
  }

  .NoData_icon {
    width: 1.5rem;
    margin-bottom: .2rem;
  }
</style>



