<template>
  <div id="register">
    <TitleBtn title="帮助注册页面"></TitleBtn>
    <div class="register_content" v-if="userData.is_reg">
      <div class="register_referrerNum">
        推荐人: {{userData.referral_code}}
      </div>
      <el-form label-position="top" :rules="rules" ref="form" :model="form" label-width="80px">
        <el-form-item label="手机号码:" prop="mobile">
          <el-input id="username" v-model="form.mobile" maxlength="11"></el-input>
        </el-form-item>
        <el-form-item label="微信号码:" prop="weixin">
          <el-input v-model="form.weixin" maxlength="20"></el-input>
        </el-form-item>
        <el-form-item label="姓名:" prop="username">
          <el-input v-model="form.username" maxlength="20"></el-input>
        </el-form-item>
        <el-form-item label="密码:" prop="password">
          <el-input type="password" v-model="form.password" maxlength="20"></el-input>
        </el-form-item>
        <el-form-item label="确认密码:" prop="checkPass">
          <el-input type="password" v-model="form.checkPass" maxlength="20"></el-input>
        </el-form-item>
        <el-form-item class="">
          <el-button 
          class="register_content_submit" 
          type="primary"
          @click="submitForm('form')"
          v-clipboard:copyhttplist="'账号:' + this.form.mobile + '密码:' + this.form.password">
          立即注册
          </el-button>
          <el-button class="register_content_reset" @click="resetForm('form')">重置</el-button>
        </el-form-item>
      </el-form>
    </div>
    <NoData v-if="!userData.is_reg" text="暂无帮助注册权限!"></NoData>
  </div>
</template>

<script>
import TitleBtn from '@/components/TitleBtn'
import NoData from '@/components/NoData'
import { getreg } from '../api/index.js'
export default {
  components: {
    TitleBtn,
    NoData
  },
  data () {
    var validatePass2 = (rule, value, callback) => {
      if (value === '') {
        callback(new Error('请再次输入密码'))
      } else if (value !== this.form.password) {
        callback(new Error('两次输入密码不一致!'))
      } else {
        callback()
      }
    }
    return {
      userData: {},
      form: {
        mobile: '',
        weixin: '',
        username: '',
        password: '',
        checkPass: ''
      },
      rules: {
        mobile: [
          { required: true, message: '请输入手机号码', trigger: 'blur' },
          { min: 11, max: 11, message: '请输入正确的手机号码', trigger: 'blur' }
        ],
        weixin: [
          { required: true, message: '请输入微信号码', trigger: 'blur' },
          { min: 6, max: 20, message: '请输入正确的微信号码', trigger: 'blur' }
        ],
        username: [
          { required: true, message: '请输入姓名', trigger: 'blur' },
          { min: 2, max: 20, message: '请输入正确的姓名', trigger: 'blur' }
        ],
        password: [
          { required: true, message: '请输入密码', trigger: 'blur' },
          { min: 4, max: 20, message: '请输入正确的密码', trigger: 'blur' }
        ],
        checkPass: [
          { required: true, validator: validatePass2, trigger: 'blur' }
        ]
      }
    }
  },
  methods: {
    resetForm(formName) {
      this.$refs[formName].resetFields()
    },
    submitForm(formName) {
      this.$refs[formName].validate((valid) => {
        if (valid) {
          getreg({
            mobile: this.form.mobile,
            weixin: this.form.weixin,
            username: this.form.username,
            password: this.form.password
          }).then(res => {
            if (res.code == 0) {
              this.$message({
                message: res.msg,
                type: 'success',
                duration: 5000
              })
              this.$confirm(`账号: ${this.form.mobile} 密码: ${this.form.password}`, '已复制到剪贴板', {
                showCancelButton: false,
                showConfirmButton: false,
                type: 'success'
              }).then(() => {}).catch(() => {})
              setTimeout(() => {
                this.$refs[formName].resetFields()
              }, 500)
            } else {
              this.$message.error({message: res.msg, duration: 1500})
            }
          })
        }
      })
    }
  },
  created() {
    this.userData = JSON.parse(localStorage.getItem('userData'))
  },
  mounted() {
  }
}
</script>

<style scoped>
  .register_content {
    width: 90%;
    margin: .3rem auto 0;
  }

  .register_referrerNum {
    line-height: .4rem;
    margin-bottom: .2rem;
  }

  #register >>> .el-form-item__error {
    top: -90%;
    right: 0;
    left: auto;
  }

  .register_content_submit {
    width: 30%;
    margin-top: .2rem;
  }

  .register_content_reset {
    width: 30%;
    margin-left: .5rem;
    margin-top: .2rem;
  }

  #register >>> .el-form-item__content:last-child {
    display: flex;
    justify-content: center;
  }

  #register >>> .el-form-item__label {
    line-height: .4rem;
  }
</style>



