<template>
  <div class="MerchantInfo">
    <TitleBtn title="商家信息"></TitleBtn>
    <div class="MerchantInfo_content"v-if="userData.business.length > 0">
      <div class="MerchantInfo_content_list" v-for="(item, index) in userData.business" :key="item.id">
        <div>姓名：{{item.username}}</div>
        <div>手机号码：{{item.mobile}}</div>
        <div>微信号：{{item.weixin}}</div>
        <el-tag :type="tagType[index]" v-if="tagType[index] != ''">{{item.uplevel_status}}</el-tag>
      </div>
    </div>
    <NoData v-if="userData.business.length <= 0"></NoData>
  </div>
</template>

<script>
import TitleBtn from '@/components/TitleBtn'
import NoData from '@/components/NoData'
export default {
  components: {
    TitleBtn,
    NoData
  },
  data () {
    return {
      userData: {
        business: []
      },
      tagType: []
    }
  },
  methods: {
  },
  created() {
    this.userData = JSON.parse(localStorage.getItem('userData'))
    this.userData.business.forEach(item => {
      if (item.uplevel_status == '通过') {
        this.tagType.push('success')
      } else if (item.uplevel_status == '拒绝') {
        this.tagType.push('danger')
      } else {
        this.tagType.push('')
      }
    })
  }
}
</script>

<style scoped>
  .MerchantInfo_content {
    width: 85%;
    margin: 0 auto;
    line-height: 1.6;
  }

  .MerchantInfo_content_list {
    background-color: #f2f2f2;
    margin-top: .4rem;
    padding: .2rem;
    position: relative;
  }

  .el-tag {
    position: absolute;
    right: .2rem;
    top: 0;
    border-radius: 0;
  }
</style>



