<template>
  <div class="ReviewHistory">
    <TitleBtn title="审核历史"></TitleBtn>
    <div class="ReviewHistory_content" v-if="historyData.length > 0">
      <div class="ReviewHistory_content_list" v-for="item in historyData" :key="item.mobile">
        <div>姓名: {{item.username}}</div>
        <div>手机号码: {{item.mobile}}</div>
        <div>微信号: {{item.weixin}}</div>
        <div>审核时间: {{item.update_time}}</div>
        <div class="ReviewHistory_content_list_btn">
          <el-tag type="info">已审核</el-tag>
        </div>
      </div>
    </div>
    <NoData v-if="historyData.length <= 0" text="您还没有相关审核纪录~"></NoData>
  </div>
</template>

<script>
import TitleBtn from '@/components/TitleBtn'
import NoData from '@/components/NoData'
import { checkLevelHistory } from '../api/index.js'
export default {
  components: {
    TitleBtn,
    NoData
  },
  data () {
    return {
      historyData: []
    }
  },
  methods: {
  },
  created() {
    checkLevelHistory({}).then(res => {
      this.historyData = res.data
    })
  }
}
</script>

<style scoped>
  .ReviewHistory_content {
    width: 85%;
    margin: .5rem auto 0;
  }

  .ReviewHistory_content_list {
    background-color: #f2f2f2;
    line-height: 1.4;
    position: relative;
    padding: .2rem;
  }

  .ReviewHistory_content_list_btn {
    height: 100%;
    position: absolute;
    right: .2rem;
    top: 0;
    display: flex;
    flex-direction: column;
    justify-content: center;
    box-sizing: border-box;
  }

  .ReviewHistory_content_list_btn >>> .el-button {
    padding: .15rem;
  }

</style>



