<template>
  <div class="MyTeam" ref="element">
    <TitleBtn :title="title"></TitleBtn>
    <div class="MyTeam_top">
      <div class="MyTeam_top_left">成员信息</div>
      <div class="MyTeam_top_right">一星及一星以上人数:{{count}}</div>
    </div>
    <NoData v-if="myTeamData.length <= 0" text="您还没有相关纪录~"></NoData>
    <div class="MyTeam_content" v-if="myTeamData.length > 0">
      <div class="MyTeam_content_list" v-for="item in myTeamData" :key="item.mobile">
        <div class="MyTeam_content_list_top">
          <div>昵称:{{item.username}}</div>
          <div>等级:{{item.level}}</div>
        </div>
        <div class="MyTeam_content_list_bottom">
          <div>手机号: {{item.mobile}}</div>
          <div>微信: {{item.weixin}}</div>
        </div>
      </div>
    </div>
    <div class="MyTeam_floor" v-if="page > 1 && !isbool">
      {{floor}}
    </div>
  </div>
</template>

<script>
import TitleBtn from '@/components/TitleBtn'
import NoData from '@/components/NoData'
import { myTeam } from '../api/index.js'
export default {
  components: {
    TitleBtn,
    NoData
  },
  data () {
    return {
      myTeamData: [],
      title: '我的团队(0人)',
      count: 0,
      page: 1,
      isbool: false,
      floor: '正在加载...'
    }
  },
  methods: {
    onScroll(e) {
      let page = this.page
      var height = this.$refs.element.offsetHeight
      if ((Math.ceil(e.currentTarget.outerHeight) + Math.ceil(e.currentTarget.pageYOffset)) >= height && this.isbool == true) {
        this.isbool = false
        this.page = page + 1
        this.getMyTeam()
      }
    },
    getMyTeam() {
      myTeam({
        page: this.page
      }).then(res => {
        this.count = res.count
        let myTeamData = this.myTeamData
        this.myTeamData = myTeamData.concat(res.data.data)
        this.title = `我的团队(${res.data.data.length}人)`
        if (this.myTeamData.length != res.data.total) {
          this.isbool = true
        } else {
          this.floor = '已全部加载'
        }
      })
    }
  },
  created() {
    this.getMyTeam()
  },
  mounted() {
    window.addEventListener('scroll', this.onScroll)
  },
}
</script>

<style scoped>
  .MyTeam_top {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: .25rem .2rem;
    border-bottom: 1px solid #f4f4f5;
  }

  .MyTeam_top_left {
    color: #666;
  }

  .MyTeam_top_right {
    color: #989898;
  }

  .MyTeam_content_list {
    padding: .3rem .2rem;
    border-bottom: 1px solid #eaeaea;
  }

  .MyTeam_content_list_top {
    color: #7a6666;
    margin-bottom: .2rem;
  }

  .MyTeam_content_list_bottom {
    color: #989898;
    font-size: .24rem;
  }

  .MyTeam_content_list_top, .MyTeam_content_list_bottom {
    display: flex;
    align-items: center;
    justify-content: space-between;
  }

  .MyTeam_floor {
    text-align: center;
    line-height: .9rem;
    font-size: .3rem;
  }
</style>



